import React from 'react';
import { Video } from 'remotion'; // absoluteFill importunu buradan da SÄ°L

interface Props {
  src: string;
  style?: React.CSSProperties;
}

export const PhoneFrame: React.FC<Props> = ({ src, style }) => {
  return (
    <div
      style={{
        ...style,
        width: 450,
        height: 900,
        borderRadius: 50,
        border: '14px solid #1a1a1a',
        backgroundColor: '#000',
        overflow: 'hidden',
        position: 'relative',
        boxShadow: '0 30px 60px -10px rgba(0,0,0,0.6)',
        zIndex: 10,
      }}
    >
      {/* Video Ä°Ã§eriÄŸi */}
      <Video
        src={src}
        style={{
          // DÃœZELTME: absoluteFill yerine manuel CSS
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          objectFit: 'cover',
        }}
        muted={true}
      />
      
      {/* Ekran Parlama Efekti */}
      <div className="absolute inset-0 bg-gradient-to-tr from-white/20 via-transparent to-transparent pointer-events-none z-20" />
    </div>
  );
};